
export default {
  props: {
    value: {
      type: Uint8Array,
      default: null,
    },
  },
  data() {
    return {
      iframeLoaded: false,
    };
  },
  watch: {
    value() {
      this.updatePdf();
    },
    iframeLoaded() {
      this.updatePdf();
    },
  },
  methods: {
    updatePdf() {
      if (!this.$refs.pdfviewer || !this.iframeLoaded || !this.value) {
        return;
      }
      const msg = new Uint8Array(this.value);
      this.$refs.pdfviewer.contentWindow.postMessage(msg, window.origin, [msg.buffer]);
    },
  },
}
